.container {
  max-width: 800px;
  margin: auto; /* Auto margin for horizontal centering */
  margin-top: 100px;
  color: white;
  padding: 20px;
  display: flex; /* Use flexbox for vertical centering */
  flex-direction: column; /* Stack children vertically */
  justify-content: center; /* Center children vertically */
  align-items: center; /* Center children horizontally */
  text-align: center; /* Center text */
}

.vs1 {
  margin-top: 50px;
  margin-bottom: 20px;
  display: flex; /* Use flexbox for horizontal alignment */
  flex-direction: column; /* Stack children vertically */
  align-items: center; /* Center children horizontally */
}

label {
  width: 100%;
  max-width: 300px;
  text-align: center;
  margin-bottom: 10px;
}

input[type="text"] {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  /* text-align: center;  */
}

button[type="submit"] {
  padding: 8px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  align-self: flex-start; /* Align button with input fields */
}

button[type="submit"]:hover {
  background-color: #0056b3;
}

.students {
  
  margin-top: 20px;
}

.students ul {
  list-style: none;
  padding: 0;
}

.students ul li {
  margin-bottom: 10px;
}

.pagination {
  color: white;
  margin-left: 100px;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pagination button {
  padding: 8px 12px;
  margin: 0 5px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.pagination button:hover {
  background-color: #0056b3;
}

.pagination button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}


/* student.css */

.upb {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 10px;
  margin-top: 15rem;
  margin-bottom: 10rem;
}

.ups {
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  max-width: 800px;
  width: 100%;
}

.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}

.message {
  font-size: 14px;
  margin-bottom: 20px;
  text-align: center;
  color: #666;
}

.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.upb {
  display: flex;
  justify-content: center;
  align-items: center;
}

.half-column {
  flex-basis: calc(50% - 20px);
  margin-bottom: 20px;
}

.full-column {
  width: 100%;
  margin-top: 20px;
}

.upv {
  display: block;
}


.submit:hover {
  background-color: #0056b3;
}

